.page-header__center {
  border-bottom: 1px solid $line-gray-all;
}

.page-header__center--noborder {
  border-bottom: none;
}

.page-header__bg {
  position: relative;
}

.page-header__bg::before {
  content: "";
  position: absolute;
  bottom: 175px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 305px;
  background-color: #64a83b;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: 320px 305px;
  z-index: -1;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/index-background-mobile@2x.jpg");
  }
}

.page-header__wrapper {
  width: auto;
  max-width: none;
  padding: 0;
}

@media (min-width: $tablet-width) {
  .page-header__center {
    width: 768px;
    margin: 0 auto;
    border-bottom: none;
  }

  .page-header__bg::before {
    display: none;
  }

  .page-header {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 708px;
  }
}

@media (min-width: $desktop-width) {
  .page-header__center {
    width: 1280px;
    margin: 0 auto;
  }

  .page-header__bg {
    min-height: 696px;

    &::before {
      display: block;
      top: 0;
      transform: translateY(0);
      width: 50%;
      min-height: 696px;
      background-image: url("../img/index-background-desktop@1x.jpg");
      background-position: right bottom;
      background-size: 720px 696px;

      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/index-background-desktop@2x.jpg");
        background-size: 720px 696px;
      }
    }
  }

  .page-header__wrapper {
    max-width: 1220px;
    padding-top: 35px;
  }
}

.page-header__text {
  margin-top: 28px;
}

@media (min-width: $tablet-width) {
  .page-header__text {
    width: 419px;
    font-weight: 400;
    font-style: normal;
    color: #111111;
    margin-left: 88px;
    margin-top: 54px;
  }
}

@media (min-width: $desktop-width) {
  .page-header__text {
    width: 419px;
    color: #111111;
    margin-left: 80px;
    margin-top: 105px;
  }
}

.page-header__header {
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .page-header__header {
    font-family: "Oswald", "Verdana", sans-serif;
    font-size: 60px;
    line-height: 60px;
    color: #111111;
    text-align: left;
  }
}

.page-header__slogan {
  font-family: "Oswald", "Verdana", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  margin-bottom: 30px;
  text-align: center;
  text-transform: uppercase;
}

@media (min-width: $tablet-width) {
  .page-header__slogan {
    font-size: 20px;
    line-height: 20px;
    color: inherit;
    text-align: left;
    margin-bottom: 20px;
  }
}

.page-header__img {
  width: 280px;
  height: 270px;
}

@media (min-width: $tablet-width) {
  .page-header__img {
    width: 709px;
    height: 609px;
  }
}

@media (min-width: $desktop-width) {
  .page-header__img {
    position: absolute;
    top: -80px;
    left: 450px;
    width: 552px;
    height: 499px;
  }
}

.page-header__button {
  padding: 11px 0;
}

@media (min-width: $tablet-width) {
  .page-header__button {
    font-size: 20px;
    line-height: 20px;
    position: absolute;
    top: 229px;
    left: 119px;
    width: 245px;
    padding: 16px 0;
  }
}

@media (min-width: $desktop-width) {
  .page-header__button {
    top: 234px;
    left: 80px;
  }
}
