@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}

body {
  min-width: 320px;
  padding: 0;
  margin: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  color: #444444;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.button {
  display: block;
  padding: 10px 0;
  font-family: "Oswald", "Verdana", sans-serif;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  text-decoration: none;
  text-align: center;
  outline: none;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}

.button--gray {
  background-color: #f2f2f2;
}

.button--gray:hover {
  background-color: #ebebeb;
}

.button--gray:active {
  background-color: #ebebeb;
  color: rgba(0, 0, 0, 0.3);
}

.container {
  max-width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}

.container--relative {
  position: relative;
  padding: 0;
}

@media (min-width: 768px) {
  .container {
    max-width: 708px;
    padding: 0 30px;
  }
  .container-helper {
    width: 200px;
    flex-shrink: 0;
    padding: 0;
    margin: 0;
  }
  .container-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1220px;
  }
  .container-helper {
    flex-shrink: 1;
    padding: 0;
  }
  .container-example {
    flex-shrink: 1;
    position: relative;
    max-width: 1440px;
  }
  .container-height {
    min-height: 455px;
    padding: 0;
  }
}

